import { useState, useContext } from "react";
import { Link } from "react-router-dom";
import Icon from '../components/Icon';
import MaterialButton from '../Atoms/MaterialButton';
import { UserContext } from '../App';

import makeStyles from '@material-ui/core/styles/makeStyles';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Avator from '@material-ui/core/Avatar';
import TextField from '@material-ui/core/TextField';
import CloseIcon from '@material-ui/icons/Close';
import CameraAltIcon from '@material-ui/icons/CameraAlt';

const useStyles = makeStyles((theme) => ({
  back: {
    maxWidth: '100%',
    minHeight: '100%',
    padding: '50px 30px',
    '@media(min-width: 678px)': {
      margin: '0 calc((100% - 678px) / 2)',
    },
    backgroundColor: 'white',
    zIndex: 5,
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0
  },
  cancel: {
    position: 'absolute',
    top: 0,
    right: 10,
    '@media (mix-width: 600px)': {
      right: 24
    },
  },
  title: {
    marginBottom: theme.spacing(3),
  },
  form: {
    width: '100%',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  box: {
    width: 120,
    height: 120,
    marginBottom: 10,
    position: 'relative'
  },
  avator: {
    width: '100%',
    height: '100%',
    zIndex: 6,
  },
  hidden: {
    width: 120,
    height: 120,
    position: 'absolute',
    top: 0,
    left: 0,
    zIndex: 7,
    opacity: 0,
    cursor: 'pointer',
    '&:hover': {
      opacity: 0.8
    }
  },
  sub: {
    marginBottom: 25,
  },
  label: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center'
  },
  typo: {
    marginRight: 5
  },
  field: {
    width: '100%',
    marginBottom: 25,
    '&.MuiInputBase-input': {
      height: 20
    },
    '& .MuiOutlinedInput-root': {
      '&:hover fieldset': {
        borderColor: '#b71c1c',
      },
      '&.Mui-focused fieldset': {
        borderColor: '#b71c1c',
      },
    },
  },
}));

function Setting(){
  const userData = useContext(UserContext);
  const [imageName, setImageName] = useState('');
  const [inputImage, setInputImage] = useState('');
  const [cropResult, setCropResult] = useState('');
  const [inputName, setInputName] = useState(userData.user_name);
  const [inputId, setInputId] = useState(userData.user_id);
  const [inputProf, setInputProf] = useState(userData.profile);
  const [errorMessage, setErrorMessage] = useState('');
  const classes = useStyles();

  const onFile = (e) => {
    if (e.target.files && e.target.files.length > 0) {
      const reader = new FileReader();
      reader.addEventListener('load', () => setInputImage(reader.result));
      reader.readAsDataURL(e.target.files[0]);

      // 
      setImageName(e.target.files[0].name.split(/(?=\.[^.]+$)/)[0]);
    };
  };

  const pushSubmit = async(e) => {
    await e.preventDefault();

    // 1
    if(inputName !== userData.user_name){
      if(inputName.length < 1){
        setErrorMessage('nameError');
        return;
      };
    };

    // id5
    if(inputId !== userData.user_id){
      if(inputId.length < 5 || inputId.match(/^[0-9A-Za-z_-]+$/) === null){
        setErrorMessage('idError');
        return;
      };
    };

    // 
    if(cropResult){
      const pictureHead = userData.picture.slice(0, 22);
      
      // -30
      if(pictureHead === 'https://audience-icons'){
        const timeCheck = await fetch('http://localhost:3100/delete', {
          method: 'POST',
          body: JSON.stringify({ image: userData.picture }),
          headers : {"Content-type" : "application/json"}
        });

        const response = await timeCheck.json();
        var resTimeCheck = response.result;
      };

      // - S3
      if(resTimeCheck === 'deleted' || pictureHead !== 'https://audience-icons'){
        const formData = await new FormData();
        await formData.append('icon', cropResult, imageName);
        
        const sendS3 = await fetch('http://localhost:3100/icon', {
          method: 'POST',
          body: formData,
        });
        const response = await sendS3.text();

        if(response !== 'uploadError'){
          var successPicture = response;
        }else{
          setErrorMessage('uploadError')
          return;
        };

      }else if(isFinite(resTimeCheck)){
        // 30
        const day = Math.round(resTimeCheck / 1000 / 60 / 60 / 24);
        setErrorMessage(day);
        return;

      }else{
        // 
        setErrorMessage('error');
        return;
      };
    };

    const data = await {
      old_id: userData.user_id,
      picture: successPicture ? successPicture : userData.picture,
      user_name: inputName,
      user_id: inputId,
      profile: inputProf
    };

    //  RDS
    fetch('http://localhost:3100/setting', {
      method: 'POST',
      body: JSON.stringify(data), 
      headers : new Headers({ "Content-type" : "application/json" })
    })
    .then(response => response.text())
    .then(result => {      
      if(result === 'success'){
        window.location.reload();
      }else if(result === 'already'){
        setErrorMessage('already');
      }else{
        setErrorMessage('authError');
      }
    })
    .catch(() => { setErrorMessage('authError') });
  };

  return(
    <>
      {/* Crop */}
      {inputImage && (
        <Icon
          setCropResult={ setCropResult }
          inputImage={ inputImage }
          setInputImage={ setInputImage }
        />
      )}

      <div className={ classes.back }>
        {/*  */}
        <IconButton
          className={ classes.cancel }
          component={ Link }
          to={ '/account/' + userData.user_id }
        >
          <CloseIcon style={{ fontSize: 25 }} />
        </IconButton>

        <form className={ classes.form } onSubmit={ pushSubmit }>
          <Typography variant="h5" gutterBottom></Typography>

          <div className={ classes.box }>
            {/*  */}
            <Avator
              className={ classes.avator }
              src={ cropResult ? URL.createObjectURL(cropResult) : userData.picture }
              alt=""
            />

            {/*  */}
            <label htmlFor="picture">
              <Avator className={ classes.hidden }>
                <CameraAltIcon style={{ fontSize: 30 }} />
              </Avator>
            </label>

            {/* display: noneforidinput */}
            <input
              style={{ display: 'none' }}
              id="picture"
              type="file"
              accept="image/png, image/jpeg"
              onChange={ onFile }
              onClick={ (e) => {
                // value
                e.target.value = '';
              }}
            />
          </div>

          <Typography
            className={ classes.sub }
            variant="subtitle1"
            color="primary"
          >
            301
          </Typography>

          <div className={ classes.label }>
            <Typography
              className={ classes.typo }
              variant="h5"
              gutterBottom
            >
              
            </Typography>
            <Typography variant="h5">({ inputName.length }/20)</Typography>
          </div>
          <TextField
            className={ classes.field }
            multiline
            defaultValue={ userData.user_name }
            variant="outlined"
            inputProps={{
              style: { fontSize: '1.5rem', lineHeight: 1 },
              maxLength: 20,
              }}
            onChange={ (e) => setInputName(e.target.value) }
          />

          <div className={ classes.label }>
            <Typography
              className={ classes.typo }
              variant="h5"
              gutterBottom
            >ID</Typography>
            <Typography variant="h5">({ inputId.length }/20)</Typography>
          </div>
          <TextField
            className={ classes.field }
            multiline
            defaultValue={ userData.user_id }
            variant="outlined"
            inputProps={{
              style: { fontSize: '1.5rem', lineHeight: 1 },
              minLength: 5,
              maxLength: 20,
              // pattern: "^[0-9A-Za-z_-]+$"
            }}
            onChange={ (e) => setInputId(e.target.value) }
          />

          <div className={ classes.label }>
            <Typography
              className={ classes.typo }
              variant="h5"
              gutterBottom
            >
              
            </Typography>
            <Typography variant="h5">({ inputProf? inputProf.length : 0 }/160)</Typography>
          </div>

          <TextField
            className={ classes.field }
            multiline
            rows={ 5 }
            defaultValue={ userData.profile }
            variant="outlined"
            inputProps={{
              style: { fontSize: '1.5rem', lineHeight: 1 },
              maxLength: 160,
              }}
            onChange={ (e) => setInputProf(e.target.value) }
          />

          <Typography
            className={ classes.sub }
            variant="h5"
            color="primary"
          >
            { (errorMessage || errorMessage === 0) && (
            isFinite(errorMessage) ? '' + (30 - errorMessage) + ''
            : errorMessage === 'idError' ? 'ID5(-)(_)'
            : errorMessage === 'nameError' ? '1'
            : errorMessage === 'uploadError' ? ''
            : errorMessage === 'authError' ? ''
            : errorMessage === 'already' ? 'ID'
            : errorMessage === 'error' && '')}
          </Typography>

          <MaterialButton pushSubmit={ pushSubmit } color='primary' text="" />
        </form>
      </div>
    </>
  )
}

export default Setting;